import React from "react";
import ReactMarkdown from "react-markdown";

const MessageBot = (props) => {
  return (
    props.message.trim() && (
      <div
        id="chatbot-box"
        style={{
          fontSize: "0.70rem",
          padding: "5px 25px",
          textAlign: "left",
          color: "#54565a",
          whiteSpace: "pre-wrap",
          wordBreak: "break-word",
          position: "initial",
          marginTop: "20px",
          marginLeft: "20px",
          fontWeight: "bold",
          borderRadius: "20px",
          border: "1px solid",
          width: "700px",
          borderColor: "#3a5eb9",
        }}
      >
        {props.message !== "" && (
          <ReactMarkdown
            components={{
              p: ({ children }) => {
                return <p>{children}</p>;
              },
              a: ({ node, children }) => {
                // eslint-disable-next-line react/jsx-no-target-blank
                return (
                  <a
                    href={`${node.properties.href}`}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {children}
                  </a>
                );
              },
              img: ({ node }) => {
                return (
                  // eslint-disable-next-line react/jsx-no-target-blank
                  <a
                    href={`${node.properties.src}`}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <img
                      src={`${node.properties.src}`}
                      alt={node.properties.alt}
                      width="170"
                    />
                  </a>
                );
              },
            }}
          >
            {props.message}
          </ReactMarkdown>
        )}
      </div>
    )
  );
};
export default MessageBot;
