import React from "react";
import { createChatBotMessage } from "react-chatbot-kit";
import Avatar from "../components/Avatar/Avatar";
import Header from "../layout/Header/Header";
import BotChatMessage from "../features/Message/MessageBot";
import Options from "../components/Options/Options";
import Facts from "../components/Facts/Facts";
import CheckboxGroup from "../components/CheckboxGroup/CheckboxGroup";
import FactsAdded from "../components/FactsAdded/FactsAdded";
import FactsFailed from "../components/FactsFailed/FactsFailed";
import Loading from "../components/Loading/Loading";


let promise;
const id = "transition2.json";
let config = {
  initialMessages: [],
  widgets: [
    {
      widgetName: "selection",
      widgetFunc: (props) => <CheckboxGroup {...props} />,
    },
    {
      widgetName: "buttons",
      widgetFunc: (props) => <Options {...props} />,
    },
    {
      widgetName: "facts",
      widgetFunc: (props) => <Facts  {...props} />,
    },
    {
      widgetName: "factssaved",
      widgetFunc: (props) => <FactsAdded  {...props} />,
    },
    {
      widgetName: "factsfailed",
      widgetFunc: (props) => <FactsFailed  {...props} />,
    },
    {
      widgetName: "loading",
      widgetFunc: (props) => <Loading  {...props} />,
    },
  ],

  customComponents: {
    botAvatar: (props) => <Avatar {...props} />,
    header: (props) => <Header {...props} />,
    botChatMessage: (props) => <BotChatMessage {...props} />
  }
};

async function main() {
  promise = import(`../../configuration/${id}`)
    .then((data) => {
      const page = data.pages[0];
      localStorage.setItem('actualmessage', JSON.stringify(page));
      const initial = [].concat(...page.example.map((message, key) => {
        if (message.type === "bubble") {
          return createChatBotMessage(
            message.content, { delay: 5 * (key * 1000), withAvatar: true }
          )
        } if (message.type === "buttons") {
          return createChatBotMessage('',
            {
              widget: "buttons",
              delay: 5 * (key * 1000),
              withAvatar: false
            }
          )
        }
        return createChatBotMessage('', { withAvatar: false })

      }
      ));
      return initial;
    });
  config.initialMessages = await promise;
}

main();

export default config;
