import React, { useState } from "react";
import TextField from "@mui/material/TextField";
import DatePickerComponent from "../DatePicker/DatePicker";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import CircularProgress from "@mui/material/CircularProgress";

import "./Facts.css";

const Facts = (props) => {
  const [descriptionArray, setDescriptionArray] = useState([
    { year: "", month: "", description: "", id: 0 },
  ]);
  const [loadingButton, setLoadingButton] = useState(false);
  const [empty, setEmpty] = useState(false);

  const handleSetYear = (event, key) => {
    descriptionArray[key].year = event;
    setDescriptionArray((descriptionArray) => [...descriptionArray]);
  };

  const handleSetMonth = (event, key) => {
    descriptionArray[key].month = event;
    setDescriptionArray((descriptionArray) => [...descriptionArray]);
  };

  const handleUpdateDescription = (event, key) => {
    descriptionArray[key].description = event.target.value;
    setDescriptionArray((descriptionArray) => [...descriptionArray]);
  };

  const handleAdddescription = () => {
    const newElement = {
      year: "",
      month: "",
      description: "",
      id: parseInt(descriptionArray.length),
    };
    setDescriptionArray((descriptionArray) => [
      ...descriptionArray,
      newElement,
    ]);
  };

  const handleRemovedescription = async (pos) => {
    const array = descriptionArray.filter((item) => item.id !== pos);
    await setDescriptionArray(array);
  };

  const isEmpty = (objectsArray) => {
    console.log(objectsArray);
    var wasNull = false;
    for (var i in objectsArray) {
      console.log(
        objectsArray[i],
        objectsArray[i].description === "",
        objectsArray[i].month === "",
        objectsArray[i].year === ""
      );
      if (
        objectsArray[i].description === "" ||
        objectsArray[i].month === "" ||
        objectsArray[i].year === ""
      )
        wasNull = true;
    }
    if (wasNull) return true;
    else return false;
  };

  const handleSave = async (array) => {
    const result = isEmpty(array);

    if (!result) {
      setEmpty(false);
      setLoadingButton(true);
      props.actionProvider.saveFacts(array);
    } else {
      setEmpty(true);
    }
  };

  return (
    <div>
      {descriptionArray.map((des, key) => {
        return (
          <div className="facts-container">
            <div className="facts-message-content">
              <div
                style={{
                  position: "relative",
                  margin: "10px 10px",
                  display: "inline-block",
                }}
              >
                <div
                  style={{
                    backgroundColor: "white",
                    border: "1px solid rgb(58, 94, 185)",
                    borderRadius: "50px",
                    position: "absolute",
                    top: "60%",
                    left: "5%",
                    transform: "translateX(-50%) translateY(-50%)",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    width: "40px",
                    height: "40px",
                  }}
                >
                  <svg width="30px" viewBox="0 0 512 512">
                    <path
                      fill="#3a5eb9"
                      d="M256 288c79.5 0 144-64.5 144-144S335.5 0 256 0 112 64.5 112 144s64.5 144 144 144zm128 32h-55.1c-22.2 10.2-46.9 16-72.9 16s-50.6-5.8-72.9-16H128C57.3 320 0 377.3 0 448v16c0 26.5 21.5 48 48 48h416c26.5 0 48-21.5 48-48v-16c0-70.7-57.3-128-128-128z"
                    ></path>
                  </svg>
                </div>

                <TextField
                  value={des.description}
                  defaultValue="Descripción del hecho"
                  onChange={(e) => handleUpdateDescription(e, key)}
                  sx={{ padding: 2 }}
                  multiline
                  rows={4}
                  style={{
                    fontSize: "0.70rem",
                    padding: "5px 25px",
                    textAlign: "left",
                    color: "#54565a",
                    whiteSpace: "pre-wrap",
                    wordBreak: "break-word",
                    position: "initial",
                    marginTop: "20px",
                    marginLeft: "20px",
                    fontWeight: "bold",
                    borderRadius: "20px",
                    border: "1px solid",
                    width: "300px",
                    borderColor: "#3a5eb9",
                  }}
                />
              </div>
            </div>
            <div className="facts-message-content">
              <DatePickerComponent
                year={des.year}
                month={des.month}
                onChangeYear={(e) => handleSetYear(e, key)}
                onChangeMonth={(e) => handleSetMonth(e, key)}
              />
            </div>
            <div className="facts-message-content">
              {key === 0 && descriptionArray.length > 1 ? (
                <Button
                  variant="contained"
                  size="small"
                  color="primary"
                  onClick={() => handleRemovedescription(key)}
                  style={{
                    margin: "5px",
                    minWidth: "150px",
                    textTransform: "none",
                    borderRadius: "10px",
                    border: "1px solid",
                  }}
                >
                  Eliminar éste hecho
                </Button>
              ) : key === 0 && descriptionArray.length === 1 ? (
                <Button
                  variant="contained"
                  size="small"
                  color="primary"
                  onClick={() => handleAdddescription()}
                  style={{
                    margin: "5px",
                    minWidth: "200px",
                    textTransform: "none",
                    borderRadius: "10px",
                    border: "1px solid",
                  }}
                >
                  Agregar más hechos
                </Button>
              ) : key > 0 && descriptionArray.length > 0 ? (
                <div style={{ width: "min-content" }}>
                  <Button
                    variant="contained"
                    size="small"
                    color="primary"
                    onClick={() => handleRemovedescription(key)}
                    style={{
                      margin: "5px",
                      minWidth: "150px",
                      textTransform: "none",
                      borderRadius: "10px",
                      border: "1px solid",
                    }}
                  >
                    Eliminar éste hecho
                  </Button>
                  <Button
                    variant="contained"
                    size="small"
                    color="primary"
                    onClick={() => handleAdddescription()}
                    style={{
                      margin: "5px",
                      minWidth: "150px",
                      textTransform: "none",
                      borderRadius: "10px",
                      border: "1px solid",
                    }}
                  >
                    Agregar más hechos
                  </Button>
                </div>
              ) : null}
            </div>
          </div>
        );
      })}

      <div style={{ alignItems: "center" }}>
        {empty && (
          <Typography variant="subtitle1" gutterBottom>
            Llene todos los datos
          </Typography>
        )}
      </div>
      <div style={{ alignItems: "center" }}>
        {!loadingButton ? (
          <Button
            variant="contained"
            size="medium"
            color="primary"
            onClick={() => handleSave(descriptionArray)}
            style={{
              margin: "20px",
              minWidth: "200px",
              textTransform: "none",
              borderRadius: "10px",
              border: "1px solid",
            }}
          >
            Guardar hechos
          </Button>
        ) : (
          <CircularProgress />
        )}
      </div>
    </div>
  );
};

export default Facts;
