const id = "transition2.json";

class ActionProvider {
  constructor(createChatBotMessage, setStateFunc, createClientMessage) {
    this.createChatBotMessage = createChatBotMessage;
    this.setState = setStateFunc;
    this.createClientMessage = createClientMessage;
  }

  traerDerechos = async () => {
    this.loading();
    const factssaved = JSON.parse(localStorage.getItem("factssaved"));
    const actualmessage = JSON.parse(localStorage.getItem("actualmessage"));

    const url = 'https://languagetutelas1.cognitiveservices.azure.com/language/:query-knowledgebases?projectName=prediccionderecho&api-version=2021-10-01&deploymentName=production'
    let promises = [];
    try {
      let result;

      factssaved.forEach(async (item) => {
        promises.push(
          new Promise(async (resolve, reject) => {
            let data = {
              top: 3,
              question: item.description
            }
            const response = await fetch(url, {
              method: "POST",
              headers: {
                "Content-type": "application/json;charset=UTF-8",
                "Ocp-Apim-Subscription-Key": "a6d6d0ce7d104937990e042a289ca17b"
              },
              body: JSON.stringify(data)
            })
            result = await response.json()
            const derechosres = result.answers.map(x => x.answer);
            resolve(derechosres);
          })
        );
      })

      Promise.all(promises).then(values => {
        const answers = values.flat()
        const valuesfil = answers.filter((x) => x !== "Ninguna");
        if (valuesfil.length > 0) {
          localStorage.setItem('derechos', JSON.stringify(valuesfil));
          this.nextPage(actualmessage.id);

        } else {
          localStorage.setItem('derechos', JSON.stringify([]));
          this.setState((prevState) => ({
            messages: prevState.messages.filter((message) => message.widget !== 'loading'),
          }));
          let factsfailed = this.createChatBotMessage('',
            {
              widget: "factsfailed",
            }
          );
          this.addMessageToState(factsfailed);
          this.addMessageToState(this.createChatBotMessage('', {
            withAvatar: false
          }
          ));
        }

      }).catch(reason => {
        console.log(reason)
      });

    } catch (error) {

    }

  }

  nextPage = async (pos) => {
    import(`../../../configuration/${id}`)
      .then((data) => {
        const page = data.pages[pos];
        let messageBot;
        let messageButtons;
        let messageComment;
        this.setState((prevState) => ({
          messages: prevState.messages.filter((message) => message.widget !== 'buttons'),
        }));

        localStorage.setItem('actualmessage', JSON.stringify(page));
        let promises = [];

        page.example.forEach(async (message, key) => {
          if (message.type === "bubble") {
            promises.push(
              new Promise((resolve, reject) => {
                messageBot = this.createChatBotMessage(
                  message.content, {
                  delay: 5 * (key * 1000), withAvatar: true,
                }
                );
                resolve(messageBot);
              })
            );


          } else if (message.type === "comment") {
            promises.push(
              new Promise((resolve, reject) => {
                //resolve(1);
                messageComment = this.createChatBotMessage(message.content,
                  {
                    delay: 5 * (key * 1000),
                    withAvatar: false
                  }
                );
                resolve(messageComment);
              })
            );

          } else if (message.type === "facts") {
            promises.push(
              new Promise((resolve, reject) => {
                //resolve(1);
                messageComment = this.createChatBotMessage('',
                  {
                    delay: 5 * (key * 1000),
                    widget: "facts",
                  }
                );
                resolve(messageComment);

              })
            );
            promises.push(
              new Promise((resolve, reject) => {
                resolve(this.createChatBotMessage('', {
                  delay: 5 * (key * 1000),
                  withAvatar: false
                }
                ));
              })
            );

          }
          else if (message.type === "selection") {
            promises.push(
              new Promise(async (resolve, reject) => {
                let messageSelected = this.createChatBotMessage('',
                  {
                    delay: 5 * (key * 1000),
                    widget: "selection",
                  }
                );
                resolve(messageSelected);
              })
            );
          }
          else if (message.type === "buttons") {
            if (page.example[key - 1].type !== "facts") {
              promises.push(
                new Promise((resolve, reject) => {
                  messageButtons = this.createChatBotMessage('',
                    {
                      widget: "buttons",
                      delay: 5 * (key * 1000),
                      withAvatar: false,

                    }
                  );
                  resolve(messageButtons);
                })

              );
            }
          }

        });

        Promise.all(promises).then(values => {
          values.forEach(item => this.addMessageToState(item));
          this.setState((prevState) => ({
            messages: prevState.messages.filter((message) => message.widget !== 'loading'),
          }));
        }).catch(reason => {
          console.log(reason)
        });
      });
  }

  loading = () => {
    let loading = this.createChatBotMessage('',
      {
        widget: "loading",
      }
    );
    this.addMessageToState(loading);
    this.addMessageToState(this.createChatBotMessage('', {
      withAvatar: false
    }
    ));
  }

  handleActionButton = () => {
    const urlpdf = 'https://cdn.rawgit.com/yohatu14/files/79da21ecf0e7ddabed60df840d9e4dd8aca3dc93/Anexo4%20ABCDE%20de%20la%20Accio%CC%81n%20de%20Tutela.pdf'
    window.open(urlpdf, '_blank', 'noopener,noreferrer');
  }

  backPage = async (pos) => {
    import(`../../../configuration/${id}`)
      .then((data) => {
        const backpage = data.pages[pos - 1];
        const back2page = data.pages[pos - 2];

        localStorage.setItem('actualmessage', JSON.stringify(back2page));

        backpage.example.forEach((object) => {
          if (object.type === "buttons") {
            this.setState((prevState) => ({
              messages: prevState.messages.filter((message) => message.widget !== 'buttons'),
            }));
          }
          if (object.type === "facts") {
            this.setState((prevState) => ({
              messages: prevState.messages.filter((message) => message.widget !== 'factssaved'),
            }));
            this.setState((prevState) => ({
              messages: prevState.messages.filter((message) => message.widget !== 'facts'),
            }));
          }

          if (object.type === "selection") {
            this.setState((prevState) => ({
              messages: prevState.messages.filter((message) => message.widget !== 'selection'),
            }));
          }
        });

        backpage.example.forEach((value) => {
          this.setState((prevState) => ({
            messages: prevState.messages.filter((message) => message.message !== value.content)
          }));
        });

        let messageButtons = this.createChatBotMessage('',
          {
            widget: "buttons",
            key: Math.random()
          }
        );
        this.addMessageToState(messageButtons);

      });
  }

  addMessageToState = (message) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        messages: [...prevState.messages, message],
      }
    });
  };

  saveFacts = (factsList) => {
    localStorage.setItem('factssaved', JSON.stringify(factsList));
    this.setState((prevState) => ({
      messages: prevState.messages.filter((message) => message.widget !== 'facts'),
    }));
    let factsSaved = this.createChatBotMessage('',
      {
        delay: 1000,
        widget: "factssaved",
      }
    );
    this.addMessageToState(factsSaved);

    this.addMessageToState(this.createChatBotMessage('', {
      delay: 2000,
      withAvatar: false
    }
    ));

  }
  removeFacts = () => {
    localStorage.removeItem('factssaved');
    localStorage.removeItem('derechos');
    this.setState((prevState) => ({
      messages: prevState.messages.filter((message) => message.widget !== 'factssaved'),
    }));
    let facts = this.createChatBotMessage('',
      {
        delay: 1000,
        widget: "facts",
      }
    );
    this.addMessageToState(facts);

    this.addMessageToState(this.createChatBotMessage('', {
      delay: 2000,
      withAvatar: false
    }
    ));
    this.setState((prevState) => ({
      messages: prevState.messages.filter((message) => message.widget !== 'factsfailed'),
    }));
  }

  showMessage = (selected, actualmessage) => {
    if (selected.id === "next") {
      this.nextPage(actualmessage.id)
    } else if (selected.id === "action") {
      this.handleActionButton()
    } else if (selected.id === "previous") {
      this.backPage(actualmessage.id)
    } else if (selected === "deletefacts") {
      this.removeFacts();
    } else if (selected === "nextfacts") {
      this.traerDerechos()
    } else if (selected === "newfacts") {
      this.removeFacts();
    }
  }

}

export default ActionProvider;
