import React from "react";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

import "./FactsAdded.css";

const FactsAd = (props) => {
  const factssaved = JSON.parse(localStorage.getItem("factssaved"));
  const actualmessage = JSON.parse(localStorage.getItem("actualmessage"));
  const [showButtons, setButtons] = React.useState(true);

  const handleChange = async (selected) => {
    setButtons(false);
    props.actionProvider.showMessage(selected, actualmessage);
  };

  return (
    <div style={{ alignItems: "center" }}>
      <Typography variant="subtitle1" gutterBottom>
        Hechos adicionados:
      </Typography>
      {factssaved.length > 0
        ? factssaved.map((item) => {
            return (
              <div
                style={{
                  position: "relative",
                  margin: "10px 40px",
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <div
                  style={{
                    backgroundColor: "white",
                    border: "1px solid rgb(58, 94, 185)",
                    borderRadius: "50px",
                    position: "absolute",
                    top: "60%",
                    left: "2%",
                    transform: "translateX(-50%) translateY(-50%)",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    width: "40px",
                    height: "40px",
                  }}
                >
                  <svg width="30px" viewBox="0 0 512 512">
                    <path
                      fill="#3a5eb9"
                      d="M256 288c79.5 0 144-64.5 144-144S335.5 0 256 0 112 64.5 112 144s64.5 144 144 144zm128 32h-55.1c-22.2 10.2-46.9 16-72.9 16s-50.6-5.8-72.9-16H128C57.3 320 0 377.3 0 448v16c0 26.5 21.5 48 48 48h416c26.5 0 48-21.5 48-48v-16c0-70.7-57.3-128-128-128z"
                    ></path>
                  </svg>
                </div>
                <div
                  id="chatbot-box"
                  style={{
                    fontSize: "0.70rem",
                    padding: "5px 25px",
                    textAlign: "left",
                    color: "#54565a",
                    whiteSpace: "pre-wrap",
                    wordBreak: "break-word",
                    position: "center",
                    marginTop: "20px",
                    marginLeft: "20px",
                    fontWeight: "bold",
                    borderRadius: "20px",
                    border: "1px solid",
                    width: "600px",
                    borderColor: "#3a5eb9",
                  }}
                >
                  <Box
                    sx={{
                      display: "block",
                      flexDirection: "row",
                      bgcolor: "background.paper",
                      margin: 2,
                    }}
                  >
                    <Box>
                      <Typography variant="bodyRegular" gutterBottom>
                        Descripción: {item.description}
                      </Typography>
                    </Box>
                    <Box>
                      <Typography variant="bodyRegular" gutterBottom>
                        Fecha: {item.year}/{item.month}
                      </Typography>
                    </Box>
                    <br></br>
                  </Box>
                </div>
              </div>
            );
          })
        : null}

      {showButtons && (
        <div style={{ alignItems: "center" }}>
          <Button
            variant="contained"
            size="medium"
            color="primary"
            onClick={() => handleChange("deletefacts")}
            style={{
              margin: "20px",
              minWidth: "200px",
              textTransform: "none",
              borderRadius: "10px",
              border: "1px solid",
            }}
          >
            Eliminar hechos
          </Button>
          <Button
            variant="contained"
            size="medium"
            color="primary"
            onClick={() => handleChange("nextfacts")}
            style={{
              margin: "20px",
              minWidth: "200px",
              textTransform: "none",
              borderRadius: "10px",
              border: "1px solid",
            }}
          >
            Continuar
          </Button>
        </div>
      )}
    </div>
  );
};

export default FactsAd;
