import React from "react";
import Button from "@mui/material/Button";

import "./Options.css";

const Options = (props) => {
  const actualmessage = JSON.parse(localStorage.getItem("actualmessage"));
  const example = actualmessage.example;

  // eslint-disable-next-line array-callback-return
  const pos = example.findIndex((object, key) => {
    if (object.type === "buttons") {
      console.log(object);
      return object.content;
    }
    if (key === example.length - 1) {
      return -1;
    }
  });
  const options = pos !== -1 ? example[pos].content : [];
  console.log(options);
  const handleChange = (selected) => {
    props.actionProvider.showMessage(selected, actualmessage);
  };

  const buttonsMarkup =
    options.length > 0 &&
    options.map((option) => (
      <Button
        variant="contained"
        size="medium"
        color="primary"
        onClick={() => handleChange(option)}
        style={{
          margin: "20px",
          minWidth: "200px",
          textTransform: "none",
          borderRadius: "10px",
          border: "1px solid",
        }}
      >
        {option.text}
      </Button>
    ));

  return <div className="options-container">{buttonsMarkup}</div>;
};

export default Options;
