import React, { useEffect } from "react";
import CheckBoxOutlineBlankIcon from "@mui/icons-material/CheckBoxOutlineBlank";
import CheckBoxIcon from "@mui/icons-material/CheckBox";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

const CheckboxGroup = ({ props }) => {
  // Checkbox Group
  const [elements, setElements] = React.useState({});

  useEffect(() => {
    const list = JSON.parse(localStorage.getItem("derechos"));
    const newlist = list.map((object) => {
      return { element: object, value: false };
    });

    setElements(newlist);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleChange = (value) => {
    const pos = elements.map((object) => object.element).indexOf(value);
    elements[pos].value = !elements[pos].value;
    setElements((elements) => [...elements]);
  };

  return (
    <div
      style={{
        position: "relative",
        margin: "10px 40px",
        display: "flex",
        justifyContent: "flex-start",
      }}
    >
      <div
        style={{
          backgroundColor: "white",
          border: "1px solid rgb(58, 94, 185)",
          borderRadius: "50px",
          position: "absolute",
          top: "60%",
          left: "2%",
          transform: "translateX(-50%) translateY(-50%)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "40px",
          height: "40px",
        }}
      >
        <svg width="30px" viewBox="0 0 512 512">
          <path
            fill="#3a5eb9"
            d="M256 288c79.5 0 144-64.5 144-144S335.5 0 256 0 112 64.5 112 144s64.5 144 144 144zm128 32h-55.1c-22.2 10.2-46.9 16-72.9 16s-50.6-5.8-72.9-16H128C57.3 320 0 377.3 0 448v16c0 26.5 21.5 48 48 48h416c26.5 0 48-21.5 48-48v-16c0-70.7-57.3-128-128-128z"
          ></path>
        </svg>
      </div>
      <div
        id="chatbot-box"
        style={{
          fontSize: "0.70rem",
          padding: "5px 25px",
          textAlign: "left",
          color: "#54565a",
          whiteSpace: "pre-wrap",
          wordBreak: "break-word",
          position: "initial",
          marginTop: "20px",
          marginLeft: "20px",
          fontWeight: "bold",
          borderRadius: "20px",
          border: "1px solid",
          width: "600px",
          borderColor: "#3a5eb9",
        }}
      >
        {elements.length > 0
          ? elements.map((item) => {
              return (
                <Box
                  sx={{
                    display: "flex",
                    flexDirection: "row",
                    bgcolor: "background.paper",
                  }}
                >
                  {!item.value && (
                    <CheckBoxOutlineBlankIcon
                      color="#1976d2"
                      size={30}
                      onClick={() => handleChange(item.element)}
                    />
                  )}
                  {item.value && (
                    <CheckBoxIcon
                      color={"primary"}
                      size={30}
                      onClick={() => handleChange(item.element)}
                    />
                  )}
                  <Typography variant="body1" gutterBottom>
                    {item.element}
                  </Typography>
                </Box>
              );
            })
          : null}
      </div>
    </div>
  );
};

export default CheckboxGroup;
