import React, { useState } from 'react';
import Chatbot from "react-chatbot-kit";
import config from "../../configuration/config";
import ActionProvider from "../../features/Actions/ActionProvider";
import MessageParser from "../../features/Actions/MessageParser";
import "./Chatbot.css";

function App() {
  const [count, setCount] = useState(false);

  return (
    <div className="App">
      <div style={{ position: "fixed", maxWidth: "100%", right: "25px", bottom: "80px" }}>
        {count === true && <Chatbot
          config={config}
          placeholderText="Haz una pregunta"
          actionProvider={ActionProvider}
          messageParser={MessageParser}
        />}

        <button
          className="app-chatbot-button"
          onClick={() => { if (count === true) { } setCount(!count) }}
        >
          {count === false ?
            <>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" class="app-chatbot-button-icon"><path d="M256 288c79.5 0 144-64.5 144-144S335.5 0 256 0 112 64.5 112 144s64.5 144 144 144zm128 32h-55.1c-22.2 10.2-46.9 16-72.9 16s-50.6-5.8-72.9-16H128C57.3 320 0 377.3 0 448v16c0 26.5 21.5 48 48 48h416c26.5 0 48-21.5 48-48v-16c0-70.7-57.3-128-128-128z"></path></svg>
            </>
            :
            <><svg className="app-chatbot-button-icon" viewBox="0 0 1000 1000"  >
              <path d="M549.3,501.5l151.3-149.7c13.8-13.6,13.9-35.7,0.3-49.5c-13.6-13.8-35.8-13.9-49.5-0.3L499.9,451.9L350.6,302.2c-13.7-13.7-35.8-13.8-49.5-0.1c-13.7,13.6-13.7,35.8-0.1,49.5l149,149.5L299.8,649.8c-13.8,13.6-13.9,35.7-0.3,49.5c6.9,6.9,15.9,10.4,24.9,10.4c8.9,0,17.8-3.4,24.6-10.1l150.5-148.8l151.7,152.2c6.8,6.9,15.8,10.3,24.8,10.3c9,0,17.9-3.4,24.7-10.2c13.7-13.7,13.7-35.8,0.1-49.5L549.3,501.5z" />
            </svg>
              <b>cerrar</b></>
          }
        </button>
      </div>
    </div>
  );
}

export default App;
